.navbar {
  background-color: #01203a;
  padding: 14px 16px 16px 16px !important;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.bars {
  font-size: 20px;
  background: none;
}

.nav-menu {
  background-color: #01203a;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 2px 0px 2px 11px;
  list-style: none;
  height: 50px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 15px;
  font-weight: 300;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 15px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #364552;
  text-decoration: none;
  color: #f5f5f5;
  border-radius: 35px;
}

.nav-menu-items {
  width: 100%;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}

.navbar-toggle {
  background-color: #01203a;
  width: 100%;
  padding-top: 16px;
  padding-left: 16px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}
