@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
.login-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  min-width: 100%;
  z-index: 0;
}

.login-container::before {
  content: "";
  position: fixed;
  left: 0;
  right: 0;
  z-index: -1;

  display: block;
  background-image: url(/webapp/static/media/bg22.1529c4bc.jpg);
  background-size: cover;

  width: 100%;
  height: 100%;
}

.inner-container {
  min-width: 350px;
}

.card {
  background-color: transparent !important;
  border: 0 !important;
}

.form-control {
  border-radius: 30px !important;
  background-color: rgba(255, 255, 255, 0.24) !important;
  border: 0 !important;
  padding: 1.25rem 0.75rem !important;
}

.form-control:focus {
  background: rgba(255, 255, 255, 0.363) !important;
}

::-webkit-input-placeholder {
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 15px !important;
  font-weight: 400 !important;
}

:-ms-input-placeholder {
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 15px !important;
  font-weight: 400 !important;
}

::-ms-input-placeholder {
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 15px !important;
  font-weight: 400 !important;
}

input,
::placeholder {
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 15px !important;
  font-weight: 400 !important;
}

::-webkit-input-placeholder {
  color: rgb(226, 226, 226) !important;
}

:-ms-input-placeholder {
  color: rgb(226, 226, 226) !important;
}

::-ms-input-placeholder {
  color: rgb(226, 226, 226) !important;
}

::placeholder {
  color: rgb(226, 226, 226) !important;
}

.links {
  color: #e89c35 !important;
  font-family: "Montserrat" !important;
  font-weight: 500;
}

.btn-primary {
  border-radius: 30px !important;
  background-color: #e89c35 !important;
  padding: 0.49rem 0.75rem !important;
  border: 0 !important;
  color: white !important;
  font-family: "Montserrat" !important;
  font-size: 0.8571em;
  font-weight: 400 !important;
}

.btn-primary:hover {
  background-color: #f0aa49 !important;
}
.text-center {
  color: white !important;
  font-family: "Montserrat" !important;
}

.navbar {
  background-color: #01203a;
  padding: 14px 16px 16px 16px !important;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.bars {
  font-size: 20px;
  background: none;
}

.nav-menu {
  background-color: #01203a;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 2px 0px 2px 11px;
  list-style: none;
  height: 50px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 15px;
  font-weight: 300;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 15px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #364552;
  text-decoration: none;
  color: #f5f5f5;
  border-radius: 35px;
}

.nav-menu-items {
  width: 100%;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}

.navbar-toggle {
  background-color: #01203a;
  width: 100%;
  padding-top: 16px;
  padding-left: 16px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}

* {
  font-family: "Montserrat";
}

